import React from 'react';
import '../App.css';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';


export default function ImgMediaCard() {

    return (
        <div class="card">
        <Card>
            <CardActionArea>
                <CardMedia
                    component="img"
                    alt="Реклама"
                    height="140"
                    image="http://www.coolwebmasters.com/uploads/posts/2012-07/1343127689_black-and-white-business-card-01.jpg"
                    title="Реклама"
                />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                        Реклама
                    </Typography>
                    <Typography variant="body2" color="textSecondary" component="p">
                    Направление в маркетинге в целях привличения внимания с помощью массовой распространение информации.
                    </Typography>
                </CardContent>
            </CardActionArea>
        </Card>
        </div>
    );
}
